
.logo {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
    font-weight: bold;
    border-radius: 50%;
  }

.header {
align-items: center;
display: flex;
height: 70px;
justify-content: space-between;
padding: 20px;
background-color: #4CAF50;
color: white;
}
.container_nav {
  display: flex;
  align-items: center;
}
.navList {
display: flex;
list-style: none;
padding: 0;
margin: 0;
}

.navItem {
margin-left: 20px;
}
.nav_fullscreen {
  display: flex !important;
}
.navLink {
color: white;
text-decoration: none;
}
.navLinkHamb {
  color: black;
}
.hamb_mobile {
  display: none !important;
}
@media screen and (max-width: 767px) {
  .hamb_mobile {
    display: flex !important;
  }
  .navList {
    display: none !important;
  }
  .logo {
    width: 40px;
    height: 40px;

  }
  .navItem {
    font-size: 0.9rem;
  }

}