/* Products.css */

.container {
    padding: 2rem;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
}
.heading {
font-size: 3rem;
margin-bottom: 1rem;
}

.productsInfo {
margin-top: 2rem;
text-align: left;
}
@media screen and (max-width: 767px) {

    .heading {
        font-size: 2rem;
    }
    .container {
        padding: 2rem 0;

    }

}