.container {

    margin: 50px auto;
    text-align: center;
    font-family: Arial, sans-serif;
  }

  .header {
    font-weight: 500;
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .subheader {
    font-size: 1.8rem;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
  }

  .paragraph {
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 1rem;
    color: #555;
  }

  .list {
    text-align: left;
    margin-left: 2rem;
    font-size: 1.1rem;
    color: #555;
  }
