

.container_products {

    width: 100%;

    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-items: center;

}
.heading {
    text-align: center;
    margin: 0 auto  30px auto;
    display: block;
}
.navWrapper {
    margin-top: 130px;
    position: relative;
    width: 100%;

    display: grid;
    justify-content: center;
    gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));


}
.productsInfo {

margin-top: 2rem;
text-align: left;
}
.button{
    transform: translate(-50%,-50%);
    position: absolute;
    left: 50%;
    top: 50%;

    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: black;
    margin: auto;
    cursor: pointer;
}
.button img {
    width: 200px;
    height: 200px;
    padding: 10px;
}

@media screen and (min-width: 767px) {

    .button_left {
        animation: buttonLeft 0.3s forwards;
    }
    .button_right {
        animation: buttonRight 0.3s forwards;
    }
    .button_center {
        animation: buttonCenter 0.3s forwards;
    }
}
@media screen and (max-width: 767px) {

    .navWrapper {
        margin-top: 40px;

    .button{
        transform:none;
        position: static;

    }
    }

}
@keyframes buttonLeft{
 0%{
    opacity: 0%;
    transform: translate(-50%,-50%);
 }
 100%{
    opacity: 100%;
    transform: translate(80%,-50%);
 }
}
@keyframes buttonRight{
    0%{
       opacity: 0%;
       transform: translate(-50%,-50%);
    }
    100%{
       opacity: 100%;
       transform: translate(-180%,-50%);
    }
   }
   @keyframes buttonCenter{
    0%{
       opacity: 0%;

    }
    100%{
       opacity: 100%;

    }
   }