.teamImgContainer {
    width: 100px;
}

.teamMemberCart {
    border: 1px solid  #499de75a;
    padding: 4px;
    -webkit-box-shadow: 10px 10px 5px -4px #499de7;
    -moz-box-shadow: 10px 10px 5px -4px #499de7;
    box-shadow: 10px 10px 5px -4px #499de7;
    animation: fadeIn 0.5s forwards;
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: row;
}
.teamDescContainer {
    padding-left: 12px;
    text-align: left;
}
.teamImgContainer img {
    object-fit: cover;
    width: 100px;
    height: 100px;
    border-radius: 50%;

}
@media screen and (max-width: 521px) {
    .teamMemberCart {
        flex-direction: column;
    }
    .teamDescContainer {
        text-align: center;
    }
}
@keyframes fadeIn{
    0%{
        opacity: 0%;

    }

    100%{
        opacity: 100%;

    }
}