.dish_cart {
    cursor: pointer;
    -webkit-box-shadow: 10px 10px 5px -4px #499de7;
    -moz-box-shadow: 10px 10px 5px -4px #499de7;
    box-shadow: 10px 10px 5px -4px #499de7;

    border-radius: 12px;
    justify-content: space-between;
    background-color: rgb(243, 239, 239);
    flex-direction: row;
    display: flex;
    max-width: 600px;
    width: 100%;
    height: 200px;
    animation: fadeIn 0.5s forwards;
    transition: 0.2s;
}
.dish_cart:hover {
    transition: 0.2s;
    background-color: #acceeb;
}
.active_cart:hover {
    background-color: #499de7;
}
.description_cart {
    line-height: 2rem;
    font-size: 1.2rem;
}
.active_dish_cart_img_container {
    width: 50% !important;

}
.active_cart .dish_cart_info{
    width: 50%;
}
.dish_cart_info {
    padding: 10px;
    width: 400px;
    display: flex;
    flex-direction: column;
    height: 100%;
}
.dish_cart_img_container {
    border-top-right-radius:  12px;
    border-bottom-right-radius:  12px;
    width: 200px;
    background-color: rgb(0, 0, 0);
}
.dish_cart_img_container img{
    border-top-right-radius:  12px;
    border-bottom-right-radius:  12px;
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.selectedCart {
    border: 2px solid #499de7;
    background-color: #f3fff3;
  }

.inactive_description {
display: none;
}
.active_description {
    display: block;

}

.active_cart {
    box-shadow: none;
    border: 1px solid white;
    z-index: 4;
    color: white;
    max-width: 70vw;
    position: fixed;
    top: 30%;
    left: 50%;
    width: 70vw;
    background-color: #499de7;

    transform: translate(-50%, -30%);
    animation: cartAnimation 0.5s forwards;

}
.inactive_cart {

    height: 200px;


}


@keyframes cartAnimation{
    0%{
        opacity: 0%;
        max-width: 600px;
        width: 100%;
        height: 200px;
    }

    100%{
        opacity: 100%;
        max-width:70vw;
        width: 70vw;
        height: 70%
    }
}
@keyframes cartAnimationmobile{
    0%{
        opacity: 0%;
        max-width: 600px;
        width: 100%;
        height: 200px;
    }

    100%{
        opacity: 100%;
        max-width:100vw;
        width: 100vw;
        height: 100vh;
        transform: translate(-50%, -50%) !important;;
    }
}
@keyframes fadeIn{
    0%{
        opacity: 0%;

    }

    100%{
        opacity: 100%;

    }
}
@media screen and (max-width: 767px) {

    .active_dish_cart_img_container {
        width: 100% !important;

    }
    .active_cart {
        padding: 4px;
        flex-direction: column;
        animation: cartAnimationmobile 0.5s forwards;

    }
    .active_cart .dish_cart_info {
        width: 100%;
    }
    .dish_cart_info {

        width: 100%;

    }
    .dish_cart_info h2{
        font-size: 1.2rem;
        width: 100%;

    }
    .dish_cart_img_container {
        width: 100%;
    }
}
