footer {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 1rem;

    bottom: 0;
  }

  footer a {
    color: #4CAF50;
    text-decoration: none;
  }
  footer {
    margin: 30px 0 0 0;
    padding: 60px 0px 30px 0px;
    background-color: #2a2a2a;
  }
  footer ul {
    padding: 0;
  }
  footer a {
    text-decoration: none;
  }
  .logoContainer {
    display: flex;
    align-items: center;

  }
  footer .logo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 20px;
    align-items: center;
    color: white;
    font-size: 1.6rem;
  }

  footer .logoContainer p{
    margin-left: 20px;
    font-size: 2rem
  }
  footer ul li {
    display: block;
    margin-bottom: 10px;
  }

  footer ul li a {
    font-size: 14px;
    color: #fff;
    transition: all 0.3s;
  }

  footer ul li a:hover {
    color: #aaa;
  }

  footer h4 {
    letter-spacing: 0.5px;
    font-size: 16px;
    color: #fff;
    font-weight: 700;
    margin-bottom: 20px;
  }

  footer .under-footer {
    margin-top: 30px;
    padding-top: 30px;
    text-align: center;
    border-top: 1px solid rgba(250, 250, 250, 0.3);
  }

  footer .under-footer ul {
    margin-top: 20px;
  }

  footer .under-footer ul li {
    display: inline-block;
    margin: 0px 10px;
  }

  footer .under-footer ul li a {
    font-size: 22px;
  }

  footer .under-footer p {
    color: #fff;
    font-size: 14px;
    font-weight: 300;
  }

  footer .under-footer a {
    color: #cff;
  }

  footer .under-footer a:hover {

    color: #c66;
  }

  @media screen and (max-width: 767px) {
    footer .logoContainer img {
        height: 50px;
        width: 50px;
      }
   footer .logoContainer {
        justify-content: center;
      }
      footer h4 {
        margin-top: 30px;
      }
      .clicked-active-label {
        display: none !important;
      }
      .clicked-inactive-label {
        display: none !important;
      }
      footer {
        padding: 9vh 0 6vh 0;
      }
  }
