
.container {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: flex;

    background-image: url("../../assets//general/cafe.jpg");
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
      min-height: calc(80vh - 100px);
      padding: 2rem;
      text-align: center;
      background-color: #f4f4f4;
      color: #ffffff;
  }

  .heading {
    font-size: 3rem;
    margin-bottom: 1rem;
  }


  .tagline {
    font-size: 1.25rem;
    margin-bottom: 2rem;
  }

  .buttonContainer {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
  }


  .button {
    background-color: #4CAF50;
    color: #fff;
    padding: 0.75rem 1.5rem;
    border-radius: 5px;
    text-decoration: none;
    font-weight: bold;
  }


  .secondaryButton {
    background-color: #fff;
    color: #4CAF50;
    padding: 0.75rem 1.5rem;
    border: 2px solid #4CAF50;
    border-radius: 5px;
    text-decoration: none;
    font-weight: bold;
  }

  @media screen and (max-width: 767px) {
    .heading {
        font-size: 2rem;
    }
    .container {
      padding: 1rem;
    }

}