.products_grid {
    margin: auto;
    display: grid;
    justify-items: center;
    gap: 30px;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));

    margin-top: 2rem;
    text-align: left;
}

@media screen and (max-width: 767px) {

    .products_grid {

        grid-template-columns: 1fr;

    }
}
